import http from '@/utils/http.js'
export default {
  post (data, success, error, baseInfo) {
    http.post(baseInfo.url, data, res => {
      if (res.success) {
        success(res.data, baseInfo)
      } else {
        error(res.errorCode, res.errorMsg, baseInfo)
      }
    })
  },
  user: {
    resetpassword (data, success, error) {
      let baseInfo = {
        url: '/user/resetpassword',
        desc: '重置密码'
      }
      this.post(data, success, error, baseInfo)
    },
    sendsmsverificationcode (data, success, error) {
      let baseInfo = {
        url: '/user/sendsmsverificationcode',
        desc: '发送短信验证码'
      }
      this.post(data, success, error, baseInfo)
    }
  },
  doproject: {
    rankinfo (data, success, error) {
      let baseInfo = {
        url: '/doproject/rankinfo',
        desc: '竞速排名信息'
      }
      this.post(data, success, error, baseInfo)
    }
  }
}
