<template>
  <div id="app">
    <div class="test" v-for="obj in testData" v-bind:key="obj.url">
      <span class="url">URL：{{obj.url}}</span>
      <span class="desc">接口名：{{obj.desc}}</span>
      <span class="state">接口状态：{{obj.state}}</span>
      <span class="code">返回错误码：{{obj.code}}</span>
      <span class="msg">返回错误信息：{{obj.msg}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  mounted () {
    this.test()
  },
  components: {
  },
  data () {
    return {
      testData: []
    }
  },
  methods: {
    test () {
      let me = this
      this.$api.doproject.rankinfo({
        'teamid': 111
      }, function (data, baseInfo) {
        me.testData.push({ url: baseInfo.url, desc: baseInfo.desc, state: '成功' })
      }, function (code, msg, baseInfo) {
        me.testData.push({ url: baseInfo.url, desc: baseInfo.desc, state: '失败', code: code, msg: msg })
      })
    }
  }
}
</script>

